.task {
    font-size: 20px;
    min-width: 400px;
    height: 50px;
    margin: 0 auto 5px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(255, 220, 104);
    transition:all 0.5s;
}

.important {
    background-color: rgb(255, 38, 0);
    color:#fff;
}
.nav {
    height: 100%;
}

.delete-button {
    cursor: pointer;
    border: none;
    width: 100px;
    height: 50px;
    font-size: 16px;
    transition: all 0.3s;
    background-color: rgb(255, 16, 16);
    outline: none;
    color: #fff;
}

.important-button {
    cursor: pointer;
    border: none;
    font-size: 16px;
    width: 100px;
    transition: all 0.3s;
    height: 50px;
    background-color: rgb(255, 123, 0);
    outline: none;
    color:#FFF;
}

.delete-button:hover {
    background-color: rgb(189, 0, 0);
}

.important-button:hover {
    background-color: rgb(218, 69, 0);
}
