.todo {
    margin: 0 auto;
    min-width: 500px;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition:all 0.5s;
}

.todo p {
    font-size: 26px;
    color: #fff;
}

.navigation {
    align-items: center;
    justify-content: center;
    margin: 20px auto;
    display: flex;
    flex-direction: row;
}

.todo-button {
    margin: 0;
    padding: 0;
    width: 50px;
    height: 50px;
    border: none;
    font-size: 18px;
    background-color: rgb(25, 175, 25);
    color: #fff;
    transition: all 0.5s;
    cursor: pointer;
    outline: none;
}

.todo-button:hover {
    background-color: rgb(17, 117, 17);
}

.todo-input {
    margin: 0;
    padding: 0;
    width: 200px;
    height: 50px;
    border: none;
    outline: none;
    line-height: 50px;
    font-size: 20px;
    background-color: rgb(255, 220, 104);
}
